// src/components/FaceMoodDetector.jsx
import React, { useEffect, useRef, useState } from "react";
import * as faceapi from "face-api.js";
import { useMood } from "../../context/MoodContext";

export default function FaceMoodDetector() {
  const videoRef = useRef(null);
  const { mood, setMood } = useMood();
  const [ready, setReady] = useState(false);
  const [error, setError] = useState("");
  const detectionIntervalRef = useRef(null);
  const [modelsLoaded, setModelsLoaded] = useState(false);

  // 1. Load models only once when component mounts
  useEffect(() => {
    async function loadModels() {
      try {
        await Promise.all([
          faceapi.nets.tinyFaceDetector.loadFromUri("/models"),
          faceapi.nets.faceExpressionNet.loadFromUri("/models"),
        ]);
        setModelsLoaded(true);
      } catch (e) {
        setError("Model loading failed");
        console.error("Model error:", e);
      }
    }
    loadModels();

    return () => {
      if (detectionIntervalRef.current) clearInterval(detectionIntervalRef.current);
    };
  }, []);

  // 2. Start camera manually
  async function startCamera() {
    if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
      setError("Camera access not supported in this browser");
      return;
    }

    let stream;
    try {
      stream = await navigator.mediaDevices.getUserMedia({
        video: true,
        audio: false,
      });

      if (videoRef.current) {
        videoRef.current.srcObject = stream;
        videoRef.current.onloadedmetadata = () => {
          videoRef.current
            .play()
            .catch((e) => console.warn("Video play blocked:", e));
          setReady(true);
          startDetection();
        };
      }
    } catch (e) {
      setError(e?.message || "Camera error");
      console.error("Camera error:", e);
    }
  }

  // 3. Mood detection logic same as before
  function startDetection() {
    if (detectionIntervalRef.current) clearInterval(detectionIntervalRef.current);

    const options = new faceapi.TinyFaceDetectorOptions({
      inputSize: 224,
      scoreThreshold: 0.5,
    });

    async function detectFace() {
      const video = videoRef.current;
      if (!video || video.readyState !== 4) return;

      try {
        const detections = await faceapi
          .detectSingleFace(video, options)
          .withFaceExpressions();

        if (detections) {
          const exps = detections.expressions;
          const top = Object.keys(exps).reduce((a, b) =>
            exps[a] > exps[b] ? a : b
          );
          setMood(mapExpressionToMood(top));
        }
      } catch (err) {
        console.error("Detection error:", err);
      }
    }

    detectionIntervalRef.current = setInterval(detectFace, 500);
  }

  const mapExpressionToMood = (exp) => {
    const map = {
      neutral: { label: "Neutral", emoji: "üòê", background: "linear-gradient(135deg, #e0e0e0, #f5f5f5)" },
      happy: { label: "Happy", emoji: "üòÑ", background: "linear-gradient(135deg, #141413ff, #ffa751)" },
      sad: { label: "Sad", emoji: "üò¢", background: "linear-gradient(135deg, #89f7fe, #66a6ff)" },
      angry: { label: "Angry", emoji: "üò†", background: "linear-gradient(135deg, #f85032, #e73827)" },
      fearful: { label: "Scared", emoji: "üò®", background: "linear-gradient(135deg, #a18cd1, #fbc2eb)" },
      disgusted: { label: "Disgusted", emoji: "ü§¢", background: "linear-gradient(135deg, #56ab2f, #a8e063)" },
      surprised: { label: "Surprised", emoji: "üò≤", background: "linear-gradient(135deg, #f7971e, #ffd200)" },
    };
    return map[exp] || map["neutral"];
  };

  return (
    <div style={{ position: "relative", width: 360, margin: "12px auto" }}>
      {error && <div style={{ color: "white" }}>{error}</div>}

      {/* Start button shown only when models are ready and camera not started */}
      {!ready && modelsLoaded && (
        <button
          onClick={startCamera}
          style={{
            padding: "10px 20px",
            fontSize: "18px",
            borderRadius: "8px",
            border: "none",
            cursor: "pointer",
            background: "#ffa751",
            color: "#000",
            marginBottom: "12px",
          }}
        >
          Start Camera
        </button>
      )}

      <div style={{ position: "relative" }}>
        <video
          ref={videoRef}
          width={360}
          height={270}
          style={{ borderRadius: 16, display: ready ? "block" : "none" }}
          muted
          playsInline
        />
        {ready && (
          <div
            style={{
              position: "absolute",
              top: "25%",
              left: "25%",
              width: "60%",
              height: "70%",
              border: "2px dashed rgba(255,255,255,0.7)",
              borderRadius: "50%",
              pointerEvents: "none",
            }}
          />
        )}
      </div>

      <div
        style={{
          textAlign: "center",
          opacity: 0.7,
          fontSize: 20,
          marginTop: 8,
          color: "white",
        }}
      >
        {ready
          ? "Align your face inside the circle"
          : modelsLoaded
          ? "Click start to enable camera"
          : "Loading models‚Ä¶"}
      </div>

      {ready && (
        <ul
          style={{
            textAlign: "center",
            fontSize: 25,
            color: "white",
            marginTop: 4,
            listStyle: "none",
            padding: 0,
          }}
        >
          <li>‚Ä¢ Face camera directly</li>
          <li>‚Ä¢ Ensure good lighting</li>
          <li>‚Ä¢ Hold still briefly</li>
        </ul>
      )}

      {mood && ready && (
        <div
          style={{
            marginTop: 16,
            padding: 12,
            borderRadius: 12,
            textAlign: "center",
            color: "white",
            background: mood.background,
            fontSize: 16,
            fontWeight: "bold",
          }}
        >
          {mood.emoji} {mood.label}
        </div>
      )}
    </div>
  );
}
